import { Component, OnInit } from '@angular/core';
import { PayrollPerubahanPayrollService, PayrollPerubahanPayrollDetailService, PayrollKomponenPerusahaanService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { Perubahan_payroll, Komponen_perusahaan } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private payrollPerubahanPayrollService: PayrollPerubahanPayrollService,
        private payrollPerubahanPayrollDetailService: PayrollPerubahanPayrollDetailService,
        private payrollKomponenPerusahaanService: PayrollKomponenPerusahaanService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private activatedRoute: ActivatedRoute
    ) { }

    id_pegawai: string = ''
    perubahan: Perubahan_payroll = {}
    perubahan_detail = [];
    id_perubahaan_payroll: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })

        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('id')) {
                    this.id_perubahaan_payroll = data.get('id');
                    this.getPreeData()
                }

            }
        )
    }

    getPreeData() {
        this.payrollPerubahanPayrollService.getPayrollPerubahanPayrollsId(this.id_perubahaan_payroll)
            .subscribe(
                data => {
                    this.perubahan = data.data;
                }
            )

        this.payrollPerubahanPayrollDetailService.getPayrollPerubahanPayrollDetails(JSON.stringify({ id_perubahan_payroll: this.id_perubahaan_payroll }))
            .subscribe(
                data => {
                    this.perubahan_detail = data.data;
                }
            )


    }


    simpan() {
        this.spinner.show()

        this.perubahan.id_pegawai = this.id_pegawai

        let param: any = {
            perubahaan: this.perubahan,
            perubahan_detail: this.perubahan_detail
        }
        this.payrollPerubahanPayrollService
            .putPayrollPerubahanPayrollsId({ id: '0', body: param })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'payroll', 'perubahaan', 'list'])
    }

}
