import { Component, OnInit } from '@angular/core';
import { PayrollPerubahanPayrollService, PayrollPerubahanPayrollDetailService, PayrollKomponenPerusahaanService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { Perubahan_payroll, Komponen_perusahaan } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(

        private payrollPerubahanPayrollService: PayrollPerubahanPayrollService,
        private payrollPerubahanPayrollDetailService: PayrollPerubahanPayrollDetailService,
        private payrollKomponenPerusahaanService: PayrollKomponenPerusahaanService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private activatedRoute: ActivatedRoute
    ) { }

    id_pegawai: string = ''
    perubahan: Perubahan_payroll = {}
    perubahan_detail = [];

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })
        this.getPreeData()
    }

    induk: Komponen_perusahaan[] = [];
    getPreeData() {
        this.payrollKomponenPerusahaanService.getPayrollKomponenPerusahaans(JSON.stringify({ is_lock: 'ya' }))
            .subscribe(
                data => {
                    data.data.forEach(element => {
                        if (element.is_lock == 'ya') {
                            this.perubahan_detail.push({ nama_komponen: element.komponen, kode_komponen: element.kode_komponen, id_komponen_perusahaan: element.id_komponen_perusahaan })
                            this.induk.push(element);
                        }
                    });
                }
            )

        this.perubahan_detail.reverse()
    }


    simpan() {
        this.spinner.show()

        this.perubahan.id_pegawai = this.id_pegawai

        let param: any = {
            perubahaan: this.perubahan,
            perubahan_detail: this.perubahan_detail
        }
        this.payrollPerubahanPayrollService
            .postPayrollPerubahanPayrolls(param)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'payroll', 'perubahaan', 'list'])
    }

}
