import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PerubahanRoutingModule } from './perubahan-routing.module';
import { ListComponent } from './list/list.component';
import { NgZorroAntdModule, NzSelectModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent],
    imports: [
        CommonModule,
        PerubahanRoutingModule,
        NgZorroAntdModule,
        NzSelectModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]
})
export class PerubahanModule { }
