import { Component, OnInit } from '@angular/core';
import { PayrollPerubahanPayrollService, PayrollPerubahanPayrollDetailService } from 'src/app/sdk/payroll/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {


    constructor(
        private payrollPerubahanPayrollService: PayrollPerubahanPayrollService,
        private payrollPerubahanPayrollDetailService: PayrollPerubahanPayrollDetailService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private pegawaiDetailService: PegawaiDetailService
    ) { }

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData = []
    induk = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    id_pegawai: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(
            data => {
                this.id_pegawai = data;
                this.searchData()
            }
        )

    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.payrollPerubahanPayrollService
            .getPayrollPerubahanPayrolls(JSON.stringify({
                id_pegawai: this.id_pegawai,
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey, sortValue: this.sortValue, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                this.induk = data.data.induk
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }




    delete(id) {
        this.spinner.show()
        this.payrollPerubahanPayrollService.deletePayrollPerubahanPayrollsId(id).subscribe(
            data => {
                this.searchData()
                this.spinner.hide()
            },
            err => {
                this.spinner.hide()
                alert('disable')
            }
        )
    }


    approve(id) {
        this.spinner.show()
        this.payrollPerubahanPayrollService.putPayrollPerubahanPayrollsId({ id: '00z', body: { id_perubahan_payroll: id } }).subscribe(
            data => {
                this.searchData()
                this.spinner.hide()
            },
            err => {
                this.spinner.hide()
                alert('disable')
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


}
